import { api } from '../api/api';

const SET_PHOTOS = 'SET_PHOTOS'
const SET_MODAL = 'SET_MODAL'
const ADD_COMMENT = 'ADD_COMMENT'

let initialState = {
  photos: null,
  comments: [],
  urlModal: null
}

const photoReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_PHOTOS:
      return {
        ...state,
        photos: action.val
      }
      case SET_MODAL:
        return {
          ...state,
          comments: action.val.comments,
          urlModal: action.val.url
        }
        case ADD_COMMENT:
        return {
          ...state,
          comments: action.text,
        }
    default: return state
  }
}

export const setPhotos = (val) => ({type: SET_PHOTOS, val})
export const setModal = (val) => ({type: SET_MODAL, val})
export const addComment = (text) => ({type: ADD_COMMENT, text})

export const onAPIPhotos = () => async (dispatch) => {
  let response = await api.getAPIPhotos()
  dispatch(setPhotos(response.data))
}

export const onModalPhoto = (id) => async (dispatch) => {
  let response = await api.getAPIModal(id)
  let comments = []
  if (response.data.comments.length > 0) {
    comments = [response.data.comments[0].text]
  }
  dispatch(setModal({
    comments,
    url: response.data.url
  }))
}

export default photoReducer