import React from 'react';
import { onAPIPhotos, onModalPhoto, addComment } from '../../redux/photoReducer';
import { connect } from 'react-redux';
import Photos from './Photos';
import ModalWin from '../Modal/ModalWin';
import Loader from '../Loader/Loader';

const PhotosContainer = props => {
  
  if (props.photos === null) props.onAPIPhotos()

  const showModal = (id) => {
    props.onModalPhoto(id)
  }

  return (
    <div className="container">
      { props.photos === null &&
        <Loader/>
      }
      <div className="photos__row">
        { props.photos != null &&
            props.photos.map(el => {
              return <Photos
                key={el.id}
                url={el.url}
                id={el.id}
                showModal={showModal}
              />
            })
        }
        <ModalWin
          urlModal={props.urlModal}
          comments={props.comments}
          addComment={props.addComment}
        />
      </div>
    </div>
  )
}
let mapStateToProps = state => {
  return {
    photos: state.photoReducer.photos,
    comments: state.photoReducer.comments,
  }
}
export default connect(mapStateToProps, { onAPIPhotos, onModalPhoto, addComment })(PhotosContainer)
