import React, { useState } from 'react';
import './Modal.scss';
import Modal from 'react-bootstrap/Modal';
import { setModal } from '../../redux/photoReducer';
import { connect } from 'react-redux';

const ModalWin = props => {

  const handleClose = () => {
    props.setModal({
      comments: [],
      url: null
    })
  }

  const [textareaValue, setTextareaValue] = useState('')
  
  const saveComment = () => {
    const textComments = [...props.comments, textareaValue]
    props.addComment(textComments)
    setTextareaValue('')
  }

  return (
    <Modal show={props.urlModal} onHide={handleClose}>
      <Modal.Header closeButton></Modal.Header>
      <Modal.Body>
        <img src={props.urlModal} alt="" className="img-fluid" />
        <div className="card">
          <div className="card-header">
            Комментарии:
          </div>
          <div className="card-body">
            {
              props.comments.map(com => {
                return<div className="comment">{com}</div>
              })
            }
          </div>
          <textarea 
            className="card-footer" 
            onChange={event => setTextareaValue(event.target.value)}
            value={textareaValue}
            placeholder="Текст комментария..."
            ></textarea>
          <button className="btn btn-success" onClick={saveComment}>Добавить</button>
      </div>
      </Modal.Body>
    </Modal>
  )
}
let mapStateToProps = state => {
  return {
    urlModal: state.photoReducer.urlModal
  }
}
export default connect(mapStateToProps, { setModal })(ModalWin)